<template>
    <div class="container accountDetailWrap">
        <div class="inner">
            <div class="locator">
                <ul>
                    <li><a href="#">메뉴</a></li>
                    <li><a href="#">회원관리</a></li>
                    <li><a href="#">회원상세</a></li>
                </ul>
            </div>
            <div class="titleWrap">
                <h3>회원 상세</h3>
                <div class="btnRightWrap">
                    <button class="btnMid color01-bg" @click="del">
                        삭제
                    </button>
                    <router-link
                        class="btnMid color03-bg"
                        tag="button"
                        :to="{ name: 'accModi', params: { userId: userId } }"
                        >수정하기</router-link
                    >
                </div>
            </div>
            <div class="tableWrap">
                <div class="tableInfo">
                    <h4 class="tableTitle">
                        회원 정보
                    </h4>
                </div>
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="12.8%" />
                            <col width="37.2%" />
                            <col width="12.8%" />
                            <col width="37.2%" />
                        </colgroup>
                        <tbody>
                            <tr>
                                <th class="txtL">
                                    아이디
                                </th>
                                <td>{{ userId }}</td>
                                <th class="txtL">
                                    이름
                                </th>
                                <td>{{ userNm }}</td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    전화번호
                                </th>
                                <td>{{ telNo }}</td>
                                <th class="txtL">
                                    성별
                                </th>
                                <td>{{ genderNm }}</td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    나이
                                </th>
                                <td>{{ age }}</td>
                                <th class="txtL">
                                    지역
                                </th>
                                <td>{{ locationNm }}</td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    등록일자
                                </th>
                                <td colspan="3">
                                    {{ regDate }}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="btnCenterWrap">
                <router-link
                    to="/account/list"
                    tag="button"
                    class="btnMasterBig color02-bg"
                    >확인</router-link
                >
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/axios';

export default {
    props: { userId: String },
    data() {
        return {
            userNm: '',
            telNo: '',
            genderCd: '',
            genderNm: '',
            locationCd: '',
            locationNm: '',
            age: '',
            regDate: '',
        };
    },
    mounted() {
        console.log(this.userId);
        axios.post('/user/detail/', { userId: this.userId }).then(res => {
            console.log(res.data.item);
            this.userNm = res.data.item.userNm;
            this.telNo = res.data.item.telNo;
            this.genderCd = res.data.item.genderCd;
            this.genderNm = res.data.item.genderNm;
            this.locationCd = res.data.item.locationCd;
            this.locationNm = res.data.item.locationNm;
            this.age = res.data.item.age;
            this.regDate = res.data.item.regDate;
        });
    },
    methods: {
        del() {
            const delList = [];
            delList.push(this.userId);
            this.$swal({
                html: '삭제하시겠습니까?',
                showCancelButton: true,
            }).then(result => {
                console.log('1', result);
                if (result.value) {
                    console.log('2');
                    axios
                        .post('/user/delete', {
                            userIdItems: delList,
                        })
                        .then(res => {
                            console.log(res);
                            if (res.data.resCode === '0000') {
                                this.$swal({
                                    html: '삭제되었습니다.',
                                }).then(re => {
                                    if (re.value) {
                                        this.$router.push('/account/list');
                                    }
                                });
                            }
                        });
                }
            });
        },
    },
};
</script>

<style></style>
