import axios from '@/axios';

const actions = {
    countAction(context) {
        context.commit('countup');
    },
    axiosGender(context) {
        return axios.post('code/list/GENDER_CD').then(res => {
            context.commit('setGenderList', res.data.items);
        });
    },
    axiosArea(context) {
        return axios.post('/code/list/LOCATION_CD').then(res => {
            context.commit('setAreaList', res.data.items);
        });
    },
};
export default actions;
