                        </colgroup>
                        <tbody>
                            <tr>
                                <th class="txtL">
                                    권한
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
