                                        </select>
                                    </div>
                                </td>
                                <th class="txtL">
                                    소속
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
                                <th class="txtL">
                                    직급
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
