                                <td class="tdInput" colspan="3">
                                    <div class="inputBorderBox">
                                        <input
                                            type="text"
                                            placeholder="검색어를 입력하세요."
                                        />
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="btnCenterWrap">
                <button class="btnMid color03-bg">
                    검색
                </button>
            </div>

            <div class="tableWrap tableListWrap">
                <div class="tableInfo">
                    <div class="listCounter">총 <span>120,000</span>건</div>
                    <div class="infoRight">
                        <span class="informTxt"
                            >※ 최근 등록일 순으로 노출됩니다.</span
                        >
                        <div class="selectBorderBox">
                            <select>
                                <option>100개</option>
                                <option>50개</option>
                                <option>30개</option>
                            </select>
