                    </div>
                </div>
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="4%" />
                            <col width="66%" />
                            <col width="10%" />
                            <col width="20%" />
                        </colgroup>
                        <thead>
                            <tr>
                                <th class="txtC tableCheckBox">
                                    <div class="checkBox">
                                        <input
                                            id="checkTotal"
                                            v-model="checkTotal"
                                            type="checkbox"
                                            @click="checkInput"
                                        />
                                        <label for="checkTotal" />
                                    </div>
                                </th>
                                <th>제목</th>
                                <th>작성자</th>
                                <th>등록일시</th>
                            </tr>
                        </thead>
                        <tbody>
                            <router-link
                                v-for="(item, idx) in list"
                                :key="idx"
                                :seqno="item.boardSeqno"
                                :to="{
                                    name: 'boardDetail',
                                    params: { boardSeqno: item.boardSeqno },
                                }"
                                tag="tr"
                            >
                                <td class="txtC tableCheckBox">
                                    <div class="checkBox">
                                        <input
                                            :id="`content${idx}`"
                                            v-model="checkbox"
                                            type="checkbox"
                                            :value="`${item.boardSeqno}`"
                                        />
                                        <label :for="`content${idx}`" />
                                    </div>
                                </td>
                                <td>{{ item.title }}</td>
                                <td>{{ item.userId }}</td>
                                <td>{{ item.regDttm }}</td>
                            </router-link>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="pageMasterWrap">
                <button class="btnQstDel color01-bg btnMid">
                    선택 삭제
                </button>
                <button class="btnReg color02-bg btnMid">
                    회원 등록
                </button>
                <div class="pagingBody">
                    <div class="pageLeft">
                        <button class="btnFirst">
                            첫페이지
                        </button>
