                        </button>
                        <button class="btnLast">
                            마지막페이지
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/axios';

export default {
    data() {
        return {
            list: [],
            checkbox: [],
            checkTotal: false,
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        checkInput() {
            const vm = this;
            console.log(this.checkTotal);
            if (this.checkTotal) {
                this.checkbox = [];
            } else {
                this.checkbox = [];
                this.list.forEach(function(item) {
                    vm.checkbox.push(item.userId);
                });
            }
        },
        getList() {
            axios.post('/board/list').then(res => {
                console.log(res.data.items);
                this.list = res.data.items;
            });
        },
    },
};
</script>

