import Vue from 'vue';
import VueRouter from 'vue-router';
// import Home from '../views/Home.vue';
import s0824 from '../views/study/s0824.vue';
import s0826 from '../views/study/s0826.vue';
import s0828 from '../views/study/s0828.vue';
import s0902 from '../views/study/s0902.vue';

import accList from '../views/account/list.vue';
import accReg from '../views/account/reg.vue';
import accDetail from '../views/account/detail.vue';
import accModi from '../views/account/modify.vue';

import boardList from '../views/board/list.vue';
import boardReg from '../views/board/reg.vue';
import boardModi from '../views/board/modify.vue';
import boardDetail from '../views/board/detail.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'accList',
        component: accList,
    },
    {
        path: '/account/list',
        name: 'accList',
        component: accList,
    },
    {
        path: '/account/reg',
        name: 'accReg',
        component: accReg,
    },
    {
        path: '/account/modify/:userId',
        name: 'accModi',
        component: accModi,
        props: true,
    },
    {
        path: '/account/detail/:userId',
        name: 'accDetail',
        component: accDetail,
        props: true,
    },
    {
        path: '/board/list',
        name: 'boardList',
        component: boardList,
    },
    {
        path: '/board/reg',
        name: 'boardReg',
        component: boardReg,
    },
    {
        path: '/board/modify/:boardSeqno',
        name: 'boardModi',
        component: boardModi,
        props: true,
    },
    {
        path: '/board/detail/:boardSeqno',
        name: 'boardDetail',
        component: boardDetail,
        props: true,
    },
    {
        path: '/study/0824',
        name: 's0824',
        component: s0824,
    },
    {
        path: '/study/0826',
        name: 's0826',
        component: s0826,
    },
    {
        path: '/study/0828',
        name: 's0828',
        component: s0828,
    },
    {
        path: '/study/0902',
        name: 's0902',
        component: s0902,
    },
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});

export default router;
