<template>
    <div>
        <input v-model="getCount" type="text" />
        <button @click="countup">+</button>
        <button @click="countdown">-</button>
    </div>
</template>

<script>
import EventBus from '@/EventBus';
import { mapGetters, mapMutations } from 'vuex';

export default {
    data() {
        return {
            comInput: '',
        };
    },
    computed: {
        ...mapGetters(['getCount']),
    },
    created() {
        EventBus.$on('event1', payload => {
            console.log('event1', payload);
            this.comInput = payload;
        });

        EventBus.$on('event3', payload => {
            console.log('event3', payload, payload.text);
            this.comInput = payload.text;
        });
    },
    methods: {
        ...mapMutations(['countup', 'countdown']),
    },
};
</script>

