<template>
    <div>
        <p>{{ msg }}</p>
        <p>for문 - 1</p>
        <p v-for="item in list" :key="item.idx">
            item : {{ item }}
            <br />
            title : {{ item.title }}
            <br />
            text : {{ item.text }}
        </p>
        <p>for문 - 2</p>
        <p v-for="(value, index) in list" :key="index">{{ value }}</p>
        <p>for문 - 3 : 컴포넌트 이용</p>
        <If1 v-for="(item, index) in list" :key="index" :item="item"></If1>
        <p>if else문</p>
        <input v-model="number" type="text" @keyup="checkNumber" />
        <button @click="resetInput">초기화 클릭</button>
        <p v-if="number == 1">1 입니다.</p>
        <p v-else-if="number == 2">2 입니다.</p>
        <p v-else>1과 2가 아닌 else 입니다.</p>
        <p>show문</p>
        <p v-show="number == 1">11111111111111111</p>
        <p v-show="visible">{{ number }}다</p>
    </div>
</template>

<script>
import If1 from '@/components/study/if1';

export default {
    components: {
        If1,
    },
    data() {
        return {
            msg: '문법 테스트',
            number: '',
            visible: false,
            list: [
                { title: '타이틀1', text: '텍스트1', idx: 1 },
                { title: '타이틀2', text: '텍스트2', idx: 2 },
                { title: '타이틀3', text: '텍스트3', idx: 3 },
                { title: '타이틀4', text: '텍스트4', idx: 4 },
                { title: '타이틀5', text: '텍스트5', idx: 5 },
                { title: '타이틀6', text: '텍스트6', idx: 6 },
            ],
        };
    },
    methods: {
        resetInput() {
            this.number = '';
        },
        checkNumber() {
            if (parseInt(this.number, 10) === 2) {
                this.visible = true;
            } else {
                this.visible = false;
            }
        },
    },
};
</script>

<style>
* {
    font-size: 15px;
}
p {
    margin-bottom: 15px;
}
input {
    width: 200px;
    height: 50px;
    line-height: 50px;
    border: 1px solid #000;
}
button {
    width: 100px;
    background: #eee;
    height: 50px;
}
</style>
