/*eslint-disable */
export const validator = {
    /**
     * 값(txt)이 비어있는지 검증
     */
    isEmpty: p_txt => {
        if (p_txt == null || p_txt == '') {
            return true;
        } else {
            return false;
        }
    },

    /**
     * 빈 어레이(arr)인지 검증
     */
    isEmptyArray: p_arr => {
        if (p_arr == null) {
            return true;
        } else if (p_arr.length < 1) {
            return true;
        } else {
            return false;
        }
    },

    /**
     * 값이 존재하는지 검증
     */
    isExistsIn: (p_arr, p_value) => {
        if (p_arr == null) {
            return false;
        }
        for (var i = 0; i < p_arr.length; i++) {
            if (p_arr[i] == p_value) {
                return true;
            }
        }
        return false;
    },

    /**
     * 값이 존재하는지 검증 [ 대 소문자 무관 ]
     */
    isExistsInIgnoreCase: (p_arr, p_value) => {
        if (p_arr == null) {
            return false;
        }
        for (var i = 0; i < p_arr.length; i++) {
            if (p_arr[i].toUpperCase() == p_value.toUpperCase()) {
                return true;
            }
        }
        return false;
    },

    /**
     * 값이 비어있거나 0인지 검증
     */
    isZero: p_txt => {
        if (p_txt == null) {
            return true;
        } else if (p_txt == '0') {
            return true;
        } else {
            return false;
        }
    },

    /**
     * 특정 문자열이 포함된 값이 존재하는지 검증
     */
    containsValueInArray: (p_arr, p_value) => {
        for (var i = 0; i < p_arr.length; i++) {
            if (p_arr[i].indexOf(p_value) != -1) {
                return true;
            }
        }
        return false;
    },

    /**
     * 양수인지 검증
     */
    isPositiveNumber: p_txt => {
        if (p_txt == null || p_txt == '') {
            return false;
        }
        if (isNaN(p_txt)) {
            return false;
        }
        if (parseInt(p_txt, 10) < 1) {
            return false;
        } else {
            return true;
        }
    },

    /**
     * 이메일 포맷인지 검증
     */
    isEmailFormat: p_txt => {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(
            p_txt,
            /[0-9a-zA-Z-_.]{3,}@[0-9a-zA-Z-_]{3,}([.][0-9a-zA-Z]{2,4}){1,}/g
        );
    },

    /**
     * 트라이패스 아이디 포맷인지 검증
     */
    isTripathEmailFormat: p_txt => {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(p_txt, /[0-9a-zA-Z-_.]/g);
    },

    /**
     * 전화번호 포맷인지 검증
     */
    isTelNoFormat: function(p_txt) {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(
            p_txt,
            /[0-9]{2,3}-[0-9]{3,4}-[0-9]{4,}/
        );
    },

    /**
     *
     *  영대문자, 숫자, 공백만 허용
     */
    isPhysicalNmFormat: function(p_txt) {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(p_txt, /[0-9A-Z-_\s]/g);
    },

    /**
     *
     * 한글, 영대문자, 숫자, 공백만 허용
     */
    isLogicalNmFormat: function(p_txt) {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(p_txt, /[0-9가-힣A-Z-_\s]/g);
    },

    /**
     *
     * 숫자인지 검증
     */
    isNumber: function(p_txt) {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(p_txt, /[0-9]/g);
    },

    /**
     * DECIMAL 데이터 포맷의 사이즈 값 유효성 검증
     */
    isDecimalSizeFormat: function(p_txt) {
        if (validator.isEmpty(p_txt)) {
            return false;
        }
        return validator.pvf_isCorrectRegExp(p_txt, /[0-9]{1,}[,][0-9]{1,}/g);
    },

    /**
     * 특정기간 사이에 포함된 날자인지 검증
     *
     * p_targetDateStr : 검증대상 일자값
     * p_postDateStr : 시작일자값
     * p_afterDateStr : 종료일자값
     */
    isDateIn: function(p_targetDateStr, p_postDateStr, p_afterDateStr) {
        p_targetDateStr = parseInt(p_targetDateStr.replace(/[^0-9]/g, ''), 10);
        p_postDateStr = parseInt(p_postDateStr.replace(/[^0-9]/g, ''), 10);
        p_afterDateStr = parseInt(p_afterDateStr.replace(/[^0-9]/g, ''), 10);
        if (
            p_targetDateStr >= p_postDateStr &&
            p_targetDateStr <= p_afterDateStr
        ) {
            return true;
        } else {
            return false;
        }
    },

    /**
     * 숫자유형만 남겨서 정수로 변환
     */
    parseNumber: function(p_value) {
        if (validator.isEmpty(p_value)) {
            return 0;
        }
        p_value = p_value + '';
        return parseInt(p_value.replace(/[^0-9.-]/gm, ''), 10);
    },

    /**
     * 비어있는경우 치환하여 반환
     */
    getIfEmptyValue: function(p_value, p_emptyValue) {
        if (validator.isEmpty(p_value)) {
            return p_emptyValue;
        } else {
            return p_value;
        }
    },

    /**
     * <br> -> \n
     */
    decodeLineFeed: function(p_value) {
        return p_value.replace(/<br>/gm, '\n');
    },

    /**
     * decoding XSS
     */
    decodeXSS: function(p_value) {
        if (validator.isEmpty(p_value)) {
            return '';
        }
        p_value = p_value.replace(/<br>/gm, '\n');
        p_value = p_value.replace(/zcribt/gm, 'script');
        p_value = p_value.replace(/&#47;/gm, '/');
        p_value = p_value.replace(/&#42;/gm, '\\*');
        p_value = p_value.replace(/&gt;/gm, '>');
        p_value = p_value.replace(/&lt;/gm, '<');
        p_value = p_value.replace(/&quot;/gm, '"');
        p_value = p_value.replace(/&apos;/gm, "'");
        p_value = p_value.replace(/&#35;/gm, '#');
        p_value = p_value.replace(/&semi;/gm, ';');
        return p_value;
    },

    /*************************** 내부함수 ***************************/

    /*
     * 정규식 매칭 검증
     */
    pvf_isCorrectRegExp: function(p_txt, p_regExp) {
        return validator.isEmpty(p_txt.replace(p_regExp, ''));
    },
};
