<template>
    <p @click="event3(item)">
        item : {{ item }}
        <br />
        title : {{ item.title }}
        <br />
        text : {{ item.text }}
    </p>
</template>

<script>
import EventBus from '@/EventBus';

export default {
    props: {
        item: {
            type: Object,
            default() {
                return { title: '', text: '' };
            },
        },
    },
    data() {
        return {};
    },
    methods: {
        event3(item) {
            console.log(item);
            EventBus.$emit('event3', item);
        },
    },
};
</script>

<style>
p {
    background: #f1f1f1;
}
</style>
