<template>
    <div>
        <p>{{ msg }}</p>
        <p>event bus1</p>
        <input v-model="inputTxt" type="text" @keyup.enter="event1" />
        <p>input1 컴포넌트</p>
        <Input1 v-show="inputTxt != ''"></Input1>
        <button @click="event2">
            list 전송
        </button>
        <If1 v-for="(item, idx) in list" :key="idx" :item="item"></If1>
    </div>
</template>

<script>
import EvnetBus from '@/EventBus';
import Input1 from '@/components/study/input1';
import If1 from '@/components/study/if1';

export default {
    components: {
        Input1,
        If1,
    },
    data() {
        return {
            msg: '문법 테스트 0826',
            inputTxt: '',
            list: [
                { title: '타이틀1', text: '텍스트1', idx: 1 },
                { title: '타이틀2', text: '텍스트2', idx: 2 },
                { title: '타이틀3', text: '텍스트3', idx: 3 },
                { title: '타이틀4', text: '텍스트4', idx: 4 },
                { title: '타이틀5', text: '텍스트5', idx: 5 },
                { title: '타이틀6', text: '텍스트6', idx: 6 },
            ],
        };
    },
    methods: {
        event1() {
            EvnetBus.$emit('event1', this.inputTxt);
        },
        event2() {},
    },
};
</script>

<style>
* {
    font-size: 15px;
}
p {
    margin-bottom: 15px;
}
input {
    width: 200px;
    height: 50px;
    line-height: 50px;
    border: 1px solid #000;
}
button {
    width: 100px;
    background: #eee;
    height: 50px;
}
</style>
