<template>
    <div>
        <div class="tableWrap">
            <div class="tableBorder">
                <table>
                    <colgroup>
                        <col width="12.8%" />
                        <col width="20.5%" />
                    </colgroup>
                    <tbody>
                        <tr>
                            <th class="txtL">
                                시작일
                            </th>
                            <td class="tdInput" colspan="3">
                                <div class="inputBorderBox">
                                    <v-date-picker
                                        ref="stDate"
                                        v-model="stDate"
                                        :popover="{ visibility: 'click' }"
                                    ></v-date-picker>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th class="txtL">
                                종료일
                            </th>
                            <td class="tdInput" colspan="3">
                                <div class="inputBorderBox">
                                    <v-date-picker
                                        ref="edDate"
                                        v-model="edDate"
                                        :min-date="stDate"
                                        :popover="{ visibility: 'click' }"
                                    ></v-date-picker>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th class="txtL">
                                이름
                            </th>
                            <td class="tdInput" colspan="3">
                                <div class="inputBorderBox">
                                    <input
                                        type="text"
                                        placeholder="검색어를 입력하세요."
                                    />
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <button @click="getDates">
            시작일 종료일 뽑기
        </button>
        <p v-show="dateInfo" v-html="dateInfo"></p>
        <p>{{ $t('message') }}</p>
    </div>
</template>

<script>
export default {
    components: {},
    data() {
        return {
            stDate: new Date(),
            edDate: new Date(),
            dateInfo: '',
        };
    },
    computed: {
        test() {
            return true;
        },
    },
    watch: {
        stDate(newVal) {
            if (newVal > this.edDate) {
                this.stDate = this.edDate;
                this.edDate = newVal;
            }
        },
        edDate(newVal) {
            if (this.stDate > newVal) {
                this.edDate = this.stDate;
                this.stDate = newVal;
            }
        },
    },
    mounted() {
        this.stDate = new Date(2020, 8, 1);
        this.minDate = this.stDate;
        // this.stDate = new Date('2020-08-01');
    },
    methods: {
        getDates() {
            this.dateInfo = `시작일 : ${this.stDate.getFullYear()} - ${this.stDate.getMonth() +
                1} - ${this.stDate.getDate()} , 종료일 : ${this.edDate}`;
        },
    },
};
</script>

<style>
button {
    margin: 1px;
}
</style>
