<template>
    <div>
        {{ getCount }}<br />
        <button @click="up">+</button>
        <button @click="down">-</button>
        <button @click="changecount">change</button>
        <button @click="interval">interval</button>
        <button @click="triggerInterVal">trggier</button>
        <button @click="interval('clear')">clear</button>
        <Input1></Input1><br />
        getGender : {{ getGender }}
        <p v-for="(item, idx) in getGender" :key="idx">
            codeNm : {{ item.itemNm }}<br />
            itemCd : {{ item.itemCd }}
        </p>
        <button @click="btnGender">성별가져오기</button>
    </div>
</template>

<script>
import Input1 from '@/components/study/input1';
import { mapGetters, mapMutations, mapActions } from 'vuex';

export default {
    components: {
        Input1,
    },
    data() {
        return {
            intervalF: null,
        };
    },
    computed: {
        ...mapGetters(['getCount', 'getGender']),
        test() {
            return true;
        },
    },
    methods: {
        ...mapMutations({ up: 'countup', down: 'countdown' }),
        ...mapActions({
            triggerInterVal: 'countAction',
            btnGender: 'axiosGender',
        }),
        changecount() {
            this.$store.commit('countchange', 10);
        },
        interval(type) {
            const vm = this;
            if (type !== 'clear') {
                this.intervalF = setInterval(function() {
                    vm.$store.commit('countup');
                }, 1000);
                return;
            }
            clearInterval(this.intervalF);
        },
    },
};
</script>

<style>
button {
    margin: 1px;
}
</style>
