            </div>
            <div class="tableWrap">
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="12.8%" />
                            <col width="20.5%" />
                            <col width="12.8%" />
                            <col width="20.6%" />
                            <col width="12.8%" />
                            <col width="20.5%" />
                        </colgroup>
                        <tbody>
                            <tr>
                                <th class="txtL">
                                    권한
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
                                <th class="txtL">
                                    소속
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
                                <th class="txtL">
                                    직급
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    계정상태
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select>
                                            <option>선택하세요.</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            <option>6</option>
                                        </select>
                                    </div>
                                </td>
                                <th class="txtL">
                                    이름
                                </th>
                                <td class="tdInput" colspan="3">
                                    <div class="inputBorderBox">
                                        <input
                                            type="text"
                                            placeholder="검색어를 입력하세요."
                                        />
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="btnCenterWrap">
                <button class="btnMid color03-bg">
                    검색
                </button>
            </div>

            <div class="tableWrap tableListWrap">
                <div class="tableInfo">
                    <div class="listCounter">총 <span>120,000</span>건</div>
                    <div class="infoRight">
                        <span class="informTxt"
                            >※ 최근 등록일 순으로 노출됩니다.</span
                        >
                        <div class="selectBorderBox">
                            <select>
                                <option>100개</option>
                                <option>50개</option>
                                <option>30개</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="3.8%" />
                            <col width="12.4%" />
                            <col width="12.4%" />
                            <col width="12.4%" />
                            <col width="12.4%" />
                            <col width="16.05%" />
                            <col width="12.84%" />
                            <col width="17.71%" />
                        </colgroup>
                        <thead>
                            <tr>
                                <th class="txtC tableCheckBox">
                                    <div class="checkBox">
                                        <input
                                            id="checkTotal"
                                            v-model="checkTotal"
                                            type="checkbox"
                                            @click="checkInput"
                                        />
                                        <label for="checkTotal" />
                                    </div>
                                </th>
                                <th>아이디</th>
                                <th>이름</th>
                                <th>전화번호</th>
                                <th>성별</th>
                                <th>나이</th>
                                <th>지역명청</th>
                                <th>등록일자</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr
                                v-for="(item, idx) in list"
                                :key="idx"
                                @click="goPage(item.userId, $event)"
                            >
                                <td class="txtC tableCheckBox">
                                    <div class="checkBox">
                                        <input
                                            :id="`content${idx}`"
                                            v-model="checkbox"
                                            :value="`${item.userId}`"
                                            type="checkbox"
                                        />
                                        <label :for="`content${idx}`" />
                                    </div>
                                </td>
                                <td>{{ item.userId }}</td>
                                <td>{{ item.userNm }}</td>
                                <td>{{ item.telNo }}</td>
                                <td>{{ item.genderNm }}</td>
                                <td>{{ item.age }}</td>
                                <td>{{ item.locationNm }}</td>
                                <td>{{ item.regDate }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="pageMasterWrap">
                <button class="btnQstDel color01-bg btnMid">
                    선택 삭제
                </button>
                <router-link
                    to="/account/reg"
                    tag="button"
                    class="btnReg color02-bg btnMid"
                    >회원 등록</router-link
                >
                <div class="pagingBody">
                    <div class="pageLeft">
                        <button class="btnFirst">
                            첫페이지
                        </button>
                        <button class="btnPrev">
                            이전페이지
                        </button>
                    </div>
                    <ul class="pagingWrap">
                        <li><a href="">1</a></li>
                        <li><a href="">2</a></li>
                        <li><a href="">3</a></li>
                        <li><a href="">4</a></li>
                        <li><a href="">5</a></li>
                        <li><a href="">6</a></li>
                        <li><a href="">7</a></li>
                        <li><a href="">8</a></li>
                        <li><a href="">9</a></li>
                        <li><a href="">10</a></li>
                        <li><a href="">11</a></li>
                        <li><a href="">12</a></li>
                    </ul>
                    <div class="pageRight">
                        <button class="btnNext">
                            다음페이지
                        </button>
                        <button class="btnLast">
                            마지막페이지
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/axios';

export default {
    data() {
        return {
            list: [],
            checkbox: [],
            checkTotal: false,
        };
    },
    watch: {
        checkbox() {
            if (this.checkbox.length === this.list.length) {
                this.checkTotal = true;
            } else {
                this.checkTotal = false;
            }
        },
    },
    mounted() {
        this.getList();
    },
    methods: {
        getList() {
            axios.post('/user/list').then(res => {
                console.log(res);
                this.list = res.data.items;
            });
        },
        checkInput() {
            const vm = this;
            console.log(this.checkTotal);
            if (this.checkTotal) {
                this.checkbox = [];
            } else {
                this.checkbox = [];
                this.list.forEach(item => {
                    vm.checkbox.push(item.userId);
                });
            }
        },
        goPage(_userId, event) {
            console.log(event.target.tagName.toUpperCase());
            if (
                event.target.tagName.toUpperCase() === 'INPUT' ||
                event.target.tagName.toUpperCase() === 'LABEL'
            ) {
                return;
            }

            this.$router.push({
                name: 'accDetail',
                params: { userId: _userId },
            });
        },
    },
};
</script>

<style></style>
