const mutations = {
    countup(state) {
        return state.count++;
    },
    countdown(state) {
        return state.count--;
    },
    countchange(state, payload) {
        console.log(payload);
        state.count = payload;
    },
    setGenderList(state, payload) {
        console.log(payload);
        state.genderList = payload;
    },
    setAreaList(state, payload) {
        console.log(payload);
        state.areaList = payload;
    },
};
export default mutations;
