<template>
    <div class="container accountDetailWrap">
        <div class="inner">
            <div class="locator">
                <ul>
                    <li><a href="#">메뉴</a></li>
                    <li><a href="#">회원관리</a></li>
                    <li><a href="#">회원상세</a></li>
                </ul>
            </div>
            <div class="titleWrap">
                <h3>회원 상세</h3>
                <div class="btnRightWrap">
                    <button class="btnMid color01-bg">
                        삭제
                    </button>
                    <button class="btnMid color03-bg">
                        수정하기
                    </button>
                </div>
            </div>
            <div class="tableWrap">
                <div class="tableInfo">
                    <h4 class="tableTitle">
                        회원 정보
                    </h4>
                </div>
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="12.8%" />
                            <col width="37.2%" />
                            <col width="12.8%" />
                            <col width="37.2%" />
                        </colgroup>
                        <tbody>
                            <tr>
                                <th class="txtL">
                                    아이디
                                </th>
                                <td>{{ userId }}</td>
                                <th class="txtL">
                                    등록일자
                                </th>
                                <td>{{ regDttm }}</td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    제목
                                </th>
                                <td colspan="3">
                                    {{ title }}
                                </td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    내용
                                </th>
                                <td colspan="3">
                                    {{ boardDscr }}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="btnCenterWrap">
                <button class="btnMasterBig color02-bg">
                    확인
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/axios';

export default {
    props: {
        boardSeqno: [String, Number],
    },
    data() {
        return {
            userId: '',
            title: '',
            boardDscr: '',
            regDttm: '',
        };
    },
    mounted() {
        console.log(typeof this.boardSeqno);

        axios
            .post('/board/detail', { boardSeqno: this.boardSeqno })
            .then(res => {
                console.log(res.data.item);
                this.userId = res.data.item.userId;
                this.title = res.data.item.title;
                this.boardDscr = res.data.item.boardDscr;
                this.regDttm = res.data.item.regDttm;
            });
    },
};
</script>

<style></style>
